import React, { Component } from 'react';
import './App.css';

import Home from './Home';

class App extends Component {
  state = { component: { Component: Home, props: {} } }

  setComponent = (Component, props) => {
    this.setState({ component: { Component, props } });
  }

  render() {
    const { Component, props } = this.state.component;
    return (
      <div className="App">
        <Component {...props} setComponent={this.setComponent} />
      </div>
    );
  }
}

export default App;



