import React from 'react';

import './index.css';

async function api(path) {
  const response = await fetch(`https://api.goalline.ca/${path}`, {
    headers: {
      GOALLINETOKEN: 'NG7OJNdvOG2nN7AVMB5I89cUYKYvR32I',
    },
  });
  return await response.json();
}

async function teamSearch(query) {
  return await api(`teams/typeahead/${query}`);
}

export async function loadGames(id) {
  return await api(`teams/${id}/schedule/games`);
}

class TeamSearchForm extends React.Component {
  onSubmit = (e) => {
    e.preventDefault();
    this.props.onSearch(this.input.value);
  }

  componentDidMount() {
    this.input.focus();
  }

  render() {
    return (
      <form className="TeamSearchForm" onSubmit={this.onSubmit}>
        <input
          className="TeamSearchForm-input"
          placeholder="Search for a team..."
          ref={(el) => { this.input = el; }}
        />
      </form>
    );
  }
}

const TeamSearchResult = ({ actions, team, viewTeam }) => {
  const { league, name, organization } = team;
  return (
    <div className="TeamSearchResult">
      <div className="TeamSearchResult-info">
        <div className="TeamSearchResult-name">{name}</div>
        <div className="TeamSearchResult-organization">{organization}</div>
        <div className="TeamSearchResult-league">{league}</div>
      </div>
      <div className="TeamSearchResult-actions">
        {actions.map(([text, handler]) => (
          <button onClick={() => handler(team)} className="Button" key={text}>
            {text}
          </button>
        ))}
      </div>
    </div>
  );
};

class TeamSearchResults extends React.Component {
  render() {
    const { loading, teams, actions } = this.props;
    return (
      <div className="TeamSearchResults">
        {loading
          ? <p className="p">Loading...</p>
          : teams.length === 0
            ? <p className="p">No results found.</p>
            : (
              <ul className="TeamSearchResults-list">
                {teams.map((team) => (
                  <li className="TeamSearchResults-item" key={team.id}>
                    <TeamSearchResult team={team} actions={actions} />
                  </li>
                ))}
              </ul>
            )}
      </div>
    );
  }
}

const Loader = ({ children, data, loading, loadingText = "Loading...", notFoundText = "Not found." }) => {
  if (loading) {
    return <p className="p">{loadingText}</p>;
  } else if ((Array.isArray(data) && data.length === 0) || !data) {
    return <p className="p">{notFoundText}</p>;
  }
  return children;
}

export const GameView = ({ game: { datetime, home_name, venue, visitor_name } }) => (
  <div className="GameView">
    <div className="GameView-teams">{visitor_name} @ {home_name}</div>
    <div className="GameView-time">{datetime.full}</div>
    <div className="GameView-venue">{venue}</div>
  </div>
);

export class TeamView extends React.Component {
  state = { loading: true, games: [] }

  async componentDidMount() {
    const games = await loadGames(this.props.team.id);
    this.setState({ loading: false, games });
  }

  render() {
    const { name } = this.props.team;
    const { games, loading } = this.state;
    return (
      <div className="TeamView">
        <div className="TeamView-name">Upcoming {name} games</div>
        <div className="TeamView-games">
          <Loader data={games} loading={loading} notFoundText="No upcoming games.">
            <ul>
              {games.map((game) => <li key={game.id}><GameView game={game} /></li>)}
            </ul>
          </Loader>
        </div>
      </div>
    );
  }
}

export class TeamSearch extends React.Component {
  state = { teams: [], loading: false }

  onSearch = async (query) => {
    this.setState({ loading: true });
    const teams = await teamSearch(query);
    this.setState({ loading: false, teams });
  }

  viewTeam = (team) => {
    this.props.setComponent(TeamView, { team });
  }

  render() {
    return (
      <div className="TeamSearch">
        <TeamSearchForm onSearch={this.onSearch} />
        <TeamSearchResults
          teams={this.state.teams}
          loading={this.state.loading}
          actions={[
            ['Add', this.props.selectTeam],
          ]}
        />
      </div>
    );
  }
}



