import React from 'react';

import { GameView, TeamSearch, loadGames } from './teams';
import './Home.css';

const storage = {
  get(name, defaultValue) {
    const value = localStorage.getItem(name);
    return (value && JSON.parse(value)) || defaultValue;
  },

  set(name, value) {
    localStorage.setItem(name, JSON.stringify(value));
  },
};

const EditTeam = ({ team, removeTeam }) => (
  <div className="EditTeam">
    <div className="EditTeam-name">{team.name}</div>
    <button className="Button" onClick={() => removeTeam(team)}>Remove</button>
  </div>
);

const EditTeams = ({ teams, removeTeam }) => (
  <div className="EditTeams">
    {teams.map((team) => <EditTeam team={team} removeTeam={removeTeam} key={team.id} />)}
  </div>
);

const HomeTeam = ({ team: { games, loading, name }, remove }) => (
  <div className="HomeTeam">
    <div className="HomeTeam-name">{name}</div>
    {loading
      ? <p>Loading games...</p>
      : games.length === 0
        ? <p>No games found.</p>
        : games.map((game) => <GameView game={game} key={game.id} />)}
  </div>
);

const HomeTeams = ({ teams }) => (
  <div className="HomeTeams">
    {teams.map((team) => <HomeTeam team={team} key={team.id} />)}
  </div>
);

export default class Home extends React.Component {
  state = { teams: storage.get('teams', {}), view: 'dashboard' }

  componentDidMount() {
    const { teams } = this.state;
    Object.keys(teams).forEach((id) => this.loadGames(teams[id]));
  }

  addTeam = () => {
    this.setState({ view: 'addTeam' });
  }

  editTeams = () => {
    this.setState({ view: 'editTeams' });
  }

  goToDashboard = () => {
    this.setState({ view: 'dashboard' });
  }

  async loadGames(team) {
    const games = await loadGames(team.id);
    this.setTeamsState(({ teams }) => {
      const updatedTeam = teams[team.id] || team;
      const updatedTeams = {
        ...teams,
        [team.id]: { ...updatedTeam, games, loading: false },
      };
      return { teams: updatedTeams };
    });
  }

  removeTeam = (team) => {
    this.setTeamsState(({ teams }) => {
      const updatedTeams = Object.keys(teams)
        // != because the key is a string and team.id is an int.
        .filter((id) => id != team.id)
        .reduce((updated, id) => ({ ...updated, [id]: teams[id] }), {});
      return { teams: updatedTeams, view: 'dashboard' };
    });
  }

  selectTeam = (team) => {
    this.props.setComponent(Home);
    this.loadGames(team);
    this.setTeamsState(({ teams }) => {
      const updatedTeams = {
        ...teams,
        [team.id]: { ...team, games: [], loading: true },
      };
      return { teams: updatedTeams, view: 'dashboard' };
    });
  }

  setTeamsState(fn) {
    this.setState((state) => {
      const newState = fn(state);
      storage.set('teams', newState.teams);
      return newState;
    });
  }

  getTeams() {
    const teamsById = this.state.teams;
    return Object.keys(teamsById).map((id) => teamsById[id]);
  }

  currentView() {
    switch (this.state.view) {
      case 'dashboard':
        {
          const teams = this.getTeams();
          return {
            title: 'Home',
            rightAction: (
              teams.length === 0
                ? <button onClick={this.addTeam}>Add team</button>
                : <button onClick={this.editTeams}>Edit teams</button>
            ),
            content: (
              <div className="Home-teams">
                {teams.length === 0
                  ? <p className="p">No teams selected.</p>
                  : <HomeTeams teams={teams} />}
              </div>
            ),
          };
        }
      case 'addTeam':
        return {
          title: 'Add team',
          leftAction: <button onClick={this.goToDashboard}>Back</button>,
          content: <TeamSearch selectTeam={this.selectTeam} className="Home-content" />,
        };
      case 'editTeams':
        return {
          title: 'Edit teams',
          leftAction: <button onClick={this.goToDashboard}>Back</button>,
          rightAction: <button onClick={this.addTeam}>Add team</button>,
          content: (
            <EditTeams
              className="Home-content"
              removeTeam={this.removeTeam}
              teams={this.getTeams()}
            />
          ),
        };
      default:
        return {
          title: 'Not Found',
          leftAction: <button onClick={this.goToDashboard}>Home</button>,
          content: <p>Not found</p>,
        };
    }
  }
  render() {
    const { leftAction, rightAction, title, content } = this.currentView();
    return (
      <div className="Home">
        <div className="Home-top-bar">
          <div className="Home-left-action">{leftAction || null}</div>
          <div className="Home-title">{title}</div>
          <div className="Home-right-action">{rightAction || null}</div>
        </div>
        <div className="Home-content">{content}</div>
      </div>
    );
  }
}



